body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nextPage {
    height: 1300px;
    width: 100px;
}

.prevPage {
    height: 1300px;
    width: 100px;
}

.nextPage:hover {
    background-image: linear-gradient(to right, rgba(255,0,0,0), rgba(255,255,255,0.3));
}

.prevPage:hover {
    background-image: linear-gradient(to left, rgba(255,0,0,0), rgba(255,255,255,0.3));
}

.arrow-left{
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}

.profil{
    background-color:rgba(0, 0, 0, 0.5);
}
.waviy {
    position: relative;
  }
  .waviy span {
    position: relative;
    display: inline-block;
    font-size: 40px;
    color: #fff;
    text-transform: uppercase;
    -webkit-animation: flip 2s infinite;
            animation: flip 2s infinite;
    -webkit-animation-delay: calc(.2s * var(--i));
            animation-delay: calc(.2s * var(--i))
  }
  @-webkit-keyframes flip {
    0%,80% {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg) 
    }
  }
  @keyframes flip {
    0%,80% {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg) 
    }
  }
.pulse:hover, 
.pulse:focus {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  box-shadow: 0 0 0 2em rgba(#fff,0);
}

@-webkit-keyframes pulse {
  0% { box-shadow: 0 0 0 0 var(--hover); }
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 var(--hover); }
}
.container-tabs {
    width: 1100px;
    margin: auto;
  }
  
  .tab-list {
    height: 50px;
    width: 100%;
    display: flex;
    list-style: none;
    font-size: 18px;
    padding: 0;
    margin: 0;
  }
  
  .tabs {
    width: 33%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f1f1f12a;
    position: relative;
    cursor: pointer;
  }
  
  .tabs:not(:last-child) {
    border-right: 1px solid rgba(0, 0, 0, 0.3);
  }
  
  .active-tabs {
    background: #000000;
    color: aliceblue;
  }
  
  .active-tabs::before {
    content: "";
    display: block;
    position: absolute;
    top: -4px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: calc(100% + 2px);
    height: 4px;
    background: #000000;
  }
  
  .content {
      background-color: #0c0c0c;
      display: none;
      content: "";
      padding: 10px;
  }
  
  .active-content {
      height: 500px;
      display: flex;
  }
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  
    .modal-main {
      position:fixed;
      background: rgb(0, 0, 0, 0.1);
      width: 80%;
      height: 80%;
      top:50%;
      left:50%;
      border-radius: 5%;
      border-color: aliceblue;
      border-width: 3px;
      color: aliceblue;
      -webkit-transform: translate(-50%,-50%);
              transform: translate(-50%,-50%);
    }
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
  
